.dropdown {
  display: inline-block;
  position: relative;
}

.dropdown .caret {
  display: inline-block;
  width: 0;
  height: 0;
  margin-left: 2px;
  vertical-align: middle;
  border-top: 4px dashed;
  border-right: 4px solid transparent;
  border-left: 4px solid transparent;
}

#dropdown-menu {
  position: absolute;
  top: 100%;
  background: #fff;
  box-shadow: 1px 1px 4px 2px rgba(51, 51, 51, 0.2);
  overflow: hidden;
  border-radius: 6px;
  list-style: none;
  margin: 5px 0 0 0;
  padding: 0;
  width: 200px;
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  transition: all 0.25s ease-in-out;
  z-index: 10;
}

#dropdown-menu.open {
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
  transition: all 0.25s ease-in-out;
}

#dropdown-menu li:hover {
  background: #e6e5e5;
}

#dropdown-menu a {
  display: block;
  padding: 10px;
  color: #000;
  text-decoration: none;
}

.dropdown-hover {
  position: relative;
  display: inline-block;
}

.dropdown-hover:hover .dropdown-hover-content, .dropdown-hover:focus .dropdown-hover-content {
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
  transition: all 0.25s ease-in-out;
}

.dropdown-hover-content {
  position: absolute;
  top: 100%;
  background: #fff;
  box-shadow: 1px 1px 4px 2px rgba(51, 51, 51, 0.2);
  overflow: hidden;
  border-radius: 6px;
  list-style: none;
  margin: 5px 0 20px 0;
  padding: 0;
  width: 200px;
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  transition: all 0.25s ease-in-out;
  z-index: 10;
}

.dropdown-hover-content li:hover {
  background: #e6e5e5;
}

.dropdown-hover-content a {
  display: block;
  padding: 10px;
  color: #000;
  text-decoration: none;
}

.select-dropdown {
  position: relative;
  display: inline-block;
}

.select-dropdown .button {
  padding-right: 30px;
}

.select-dropdown .button:after {
  content: "";
  position: absolute;
  top: 46%;
  right: 10px;
  width: 0;
  height: 0;
  border: 5px solid transparent;
  border-top-color: #fff;
}

.select-dropdown__list {
  position: absolute;
  top: 100%;
  background: #fff;
  box-shadow: 1px 1px 4px 2px rgba(51, 51, 51, 0.2);
  overflow: hidden;
  border-radius: 6px;
  list-style: none;
  margin: 5px 0 20px 0;
  padding: 0;
  width: 200px;
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  transition: all 0.25s ease-in-out;
  z-index: 10;
  left: 0;
}

.select-dropdown.is-open .select-dropdown__list {
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
  transition: all 0.25s ease-in-out;
}

.select-dropdown__item {
  padding: 10px;
  cursor: pointer;
  color: #000;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  transition: all .25s ease-in-out;
}

.select-dropdown__item:hover {
  background-color: #E6E9ED;
}
