@import '_reset.scss';
@import '_mixins.scss';
@import "../../mixins/breakpoint";

// Font Family
$base-font-family: 'Poppins', sans-serif;

// Base Font for HTML
$base-font-size: 16px;

// Paragraph Styles
$paragraph-color: #505050;
$paragraph-size: rem(16px);
$paragraph-size-1400: rem(24px);
$paragraph-line-height: 1.5em;
$paragraph-line-height-1400: 1.3em;
$paragraph-margin: 20px;

// Header Styles
$header-color: $black-light-dark;
$header-size: rem(40px);
$header-size-1400: rem(60px);
$header-line-height: 1.25em;
$header-margin: $paragraph-margin * 1.2;

// Blockquote Styles
$blockquote-color: #505050;
$blockquote-size: rem(23px);
$blockquote-line-height: 1.5em;
$blockquote-margin: $paragraph-margin * 1.5;

// hr
$hr-margin: 15px;

// Type Misc
$font-weight-bold: 800;
$border-color: #dadada;

/*------------------------------------*\
    # Type
\*------------------------------------*/

// Make type normal across browsers
*,
html,
body {
  font-size: $base-font-size;
  font-family: $base-font-family;
}

// Headers
h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: $header-margin * 1.5;
  color: $header-color;
  line-height: $header-line-height;
  font-weight: 500;

  &:first-child {
    margin-top: 0;
  }
}

h1 {
  font-size: $header-size;
  margin-bottom: $header-margin;

  @include mq('desktop-wide') {
    font-size: $header-size-1400;
  }
}

h2 {
  font-size: $header-size / 1.3;
  margin-bottom: $header-margin / 1.3;

  @include mq('desktop-wide') {
    font-size: $header-size-1400 / 1.3;
  }
}

h3 {
  font-size: $header-size / 1.5;
  margin-bottom: $header-margin / 1.5;

  @include mq('desktop-wide') {
    font-size: $header-size-1400 / 1.5;
  }
}

h4 {
  font-size: $header-size / 1.7;
  margin-bottom: $header-margin / 1.7;

  @include mq('desktop-wide') {
    font-size: $header-size-1400 / 1.7;
  }
}

h5 {
  font-size: $header-size / 1.8;
  margin-bottom: $header-margin / 1.8;

  @include mq('desktop-wide') {
    font-size: $header-size-1400 / 1.8;
  }
}

h6 {
  font-size: $header-size / 2;
  margin-bottom: $header-margin / 2;

  @include mq('desktop-wide') {
    font-size: $header-size-1400 / 2;
  }
}

// Paragraphs
p {
  margin-bottom: $paragraph-margin;
  font-size: $paragraph-size;
  line-height: $paragraph-line-height;
  color: $paragraph-color;

  @include mq('desktop-wide') {
    font-size: $paragraph-size-1400;
    line-height: $paragraph-line-height-1400;
  }

  &:last-of-type {
    margin-bottom: 0;
  }
}

// hr
hr {
  height: 1px;
  margin: $hr-margin 0;
  border: none;
  background-color: $border-color;
}

// Blockquote
blockquote {
  margin: $blockquote-margin 0;
  font-size: $blockquote-size;
  line-height: $blockquote-line-height;
  color: $blockquote-color;
  text-align: center;
  font-style: italic;
}

// Tables
table {
  width: 100%;
  margin: $blockquote-margin 0;
  border-collapse: collapse;
}

table,
td,
th {
  border: 1px solid $border-color;
  text-align: left;
}

th {
  font-weight: $font-weight-bold;
}

th,
td {
  padding: 15px;
}

// Figure
figure {
  margin: $blockquote-margin 0;

  img {
    margin: 0;
  }
}

figcaption {
  margin-top: 10px;
  color: lighten($paragraph-color, 10%);
  font-size: $paragraph-margin * 0.8;
}

// Code
pre {
  display: block;
  margin: 0 0 40px 0;
  padding: 20px;
  background-color: lighten($border-color, 12%);
  border: 1px solid $border-color;
  overflow: auto;
  border-radius: 3px;

  code {
    position: static;
    padding: 0;
    border: none;
    line-height: 1.5em;
  }
}

code {
  position: relative;
  top: -0.2em;
  padding: 3px;
  font-family: Courier New, Courier, monospace;
  font-size: $paragraph-size / 1.2;
  color: darken($border-color, 70%);
  line-height: 1em;
  pointer-events: none;
  border: 1px solid $border-color;
  border-radius: 3px;
}

// Misc Styles
em,
i,
.italic {
  font-style: italic;
}

strong,
b,
.bold {
  font-weight: $font-weight-bold;
}

img {
  display: block;
  max-width: 100%;
  margin: $blockquote-margin 0;
}
